
%clearfix {
  &::after {
    clear: both;
    content: '';
    display: table;
  }
}

/**
 * Adds a breakpoint and places the given content inside that breakpoint.
 *
 * @param $size, the breakpoint size, defined by bootstrap-sass
 * in node_modules/bootstrap-sass/assets/stylesheets/bootstrap/_variables.scss
 * Possible values are e.g. $screen-sm or $screen-md.
 */
@mixin breakpoint($size) {
  @media screen and (min-width: $size) {
    @content;
  }
}
