.confProgram__key {
  border: 2px solid $color-gallery;
  margin-bottom: 32px;
  padding: 0;

  &Item {
    display: inline-block;
    list-style: none;
    padding: 8px 16px;
  }

  i {
    padding-right: 4px;
  }
}

.confProgram__wrapper {
  max-width: 90%;
  margin: 0 auto;
}

.confProgram__table th,
.confProgram__table td {
  text-align: left;
}

.confProgram__table.layout {
  width: 100%;
  border-collapse: collapse;
}

.confProgram__table.display {
  margin: 1em 0;
}

.confProgram__table.display th,
.confProgram__table.display td {
  padding: .5em 1em;
}

.confProgram__table.display th {
  background-image: $gradient-moinworld-accessible;
  text-align: center;
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: white;
  border-right: 16px solid white;
}

.confProgram__table.display td {
}

.confProgram__table.responsive-table {
}

.confProgram__item {
  vertical-align: top;
  padding: 16px;
  text-align: left;
  background-color: $color-gallery;
  border: 16px solid white;
}

.confProgram__time {
  background-color: transparent;
  min-width: 130px;
  color: $color-mulberry;
  border-left: 3px solid $color-mulberry;
  font-size: 14px;
}

.confProgram__item__noTalk {
  background-color: transparent;
}

.confProgram__item__talkTime {
  text-align: left;
  font-size: 14px;
  letter-spacing: 0;

  i {
    padding-right: 4px;
  }
}

.confProgram__item__talkName {
  text-align: left;
  line-height: 1.2;
}

.confProgram__item__speakerName {
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.confProgram__item__break {
  text-align: center !important;
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: rgba($color-mona-lisa, 0.5);
  color: #515151;
  padding: 16px !important;
}

@media (max-width: 55em) {
  .confProgram__item {
    border: none;
    background-color: $color-white;
  }

  .confProgram__time {
    background-image: $gradient-moinworld-accessible;
    font-size: 14px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: white;
    padding: 16px;
  }

  .confProgram__item__noTalk {
    display: none !important;
  }

  .confProgram__item__break {
    text-align: left !important;
    background-color: rgba($color-mona-lisa, 0.5);
  }

  .confProgram__table.responsive-table {
    box-shadow: none;
  }

  .confProgram__table.responsive-table thead {
    display: none;
  }

  .confProgram__table.display th,
  .confProgram__table.display td {
    padding: 1em;
  }

  .confProgram__table.responsive-table td:nth-child(1):before {
    content: 'Time';
  }

  .confProgram__table.responsive-table td:nth-child(2):before {
    content: 'Stage 1';
  }

  .confProgram__table.responsive-table td:nth-child(3):before {
    content: 'Stage 2';
  }

  .confProgram__table.responsive-table td:nth-child(4):before {
    content: 'Stage 3';
  }

  .confProgram__table.responsive-table td:nth-child(5):before {
    content: 'Stage 4';
  }

  .confProgram__table.responsive-table td.confProgram__item__break:before {
    content: '';
  }

  .confProgram__table.responsive-table td:nth-child(1),
  .confProgram__table.responsive-table td:nth-child(2),
  .confProgram__table.responsive-table td:nth-child(3),
  .confProgram__table.responsive-table td:nth-child(4),
  .confProgram__table.responsive-table td:nth-child(5) {
    padding-left: 100px;
  }

  .confProgram__table.responsive-table td:nth-child(1):before,
  .confProgram__table.responsive-table td:nth-child(2):before,
  .confProgram__table.responsive-table td:nth-child(3):before,
  .confProgram__table.responsive-table td:nth-child(4):before,
  .confProgram__table.responsive-table td:nth-child(5):before {
    position: absolute;
    left: 1em;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
  }

  .confProgram__table.responsive-table tr,
  .confProgram__table.responsive-table td {
    display: block;
  }

  .confProgram__table.responsive-table tr {
    position: relative;
    margin-bottom: 1em;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
  }

  .confProgram__table.responsive-table td {
    border-top: none;
  }
}
