/* universal box */
.box {
  background: #fff;
  margin: 0 0 30px;
  box-sizing: border-box;
  padding: 20px 0;

  &--bordered {
    border: solid 1px #ccc;
  }

  &--no-background {
    background: 0;
  }



  & .box-header {
    background: #f7f7f7;
    margin: -20px 0 20px;
    padding: 20px;
    border-bottom: solid 1px #eeeeee;
    text-transform: uppercase;
    letter-spacing: 0.08em;

    &::before,
    &::after {
      content: ' ';
      display: table;
    }

    &::after {
      clear: both;
    }
  }
  & .box-footer {
    background: #f7f7f7;
    margin: 30px 0 -20px;
    padding: 20px;
    border-top: solid 1px #eeeeee;

    &::before,
    &::after {
      content: ' ';
      display: table;
    }

    &::after {
      clear: both;
    }

    @media (max-width: 991px) {
      .btn {
        margin-bottom: 20px;
      }
    }

  }

}
