#footer {
  background: $color-emperor;
  padding: 64px 0 32px;
  color: $color-white;
}

#footer h1,
#footer h2,
#footer h3,
#footer h4,
#footer h5,
#footer h6,
#footer p {
  color: $color-white;
  line-height: 1.2;
}

.footer__navItem {
  font-size: 14px;
  font-weight: 700;
  line-height: 2;
  display: block;
  color: $color-white;
  text-transform: uppercase;
  letter-spacing: 2px;

  &:hover {
    color: $color-white;
    text-decoration: underline;
  }

  &:active {
    color: $color-white;
    text-decoration: underline;
  }

  &:focus {
    color: $color-white;
    text-decoration: underline;
  }
}

.language-chooser {
}

.language-list {
  .active {
    text-decoration: underline;
  }
}

#footer ul {
  padding-left: 0;
  list-style: none;
}
#footer ul a {
  color: $color-white;
}
#footer ul a:hover {
  color: $color-moinworld;
  text-decoration: none;
}
#footer .photostream div {
  float: left;
  display: block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 33%;
  padding: 7.5px;
  overflow: hidden;
}
#footer .photostream div a {
  border: solid 1 px #eeeeee;
}
#footer .photostream div img {
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
#footer .photostream div:hover img {
  -webkit-transform: scale(1.1, 1.1);
  -ms-transform: scale(1.1, 1.1);
  -o-transform: scale(1.1, 1.1);
  transform: scale(1.1, 1.1);
}
#footer .blog-entries .item {
  clear: both;
  padding: 5px 0;
  margin-bottom: 10px;
  border-bottom: solid 1px #555555;
}
#footer .blog-entries .item .image {
  float: left;
  width: 15%;
  margin-right: 10px;
}
#footer .blog-entries .item .name {
  width: 75%;
  margin-left: 10px;
  display: table-cell;
  vertical-align: middle;
}
#footer .blog-entries .item .name h5 {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 12px;
}
#footer .blog-entries .item .name h5 a {
  color: #eeeeee;
}
#footer .blog-entries .item .text {
  width: 100%;
  clear: both;
}
#footer .blog-entries .item:last-child {
  border-bottom: none;
  margin-bottom: 0;
}
#footer .social a {
  color: #555555;
  font-size: 25px;
  margin: 0 10px 0 0;
}
#footer .social a:hover {
  color: $color-moinworld;
}

#copyright {
  padding-top: 64px;
  font-size: 12px;

  a {
    text-decoration: underline;

    &:hover {
      color: $color-white;
      opacity: 0.7;
    }
  }
}

.footer__social-media {
  margin-top: 64px;

  i {
    font-size: 24px;
    margin-right: 16px;
  }

  a {
    text-decoration: underline;

    &:hover {
      color: $color-white;
      opacity: 0.7;
    }
  }
}
