/* Navbar */
.navbar-toggle {

  $max-size: 44px;

  // overwrite bootstrap settings
  .navbar-default & {
    background-color: #fff;
    border-color: $color-moinworld;
    color: $color-moinworld;
    float: right;
    outline: 0;
    max-height: $max-size;
    max-width: $max-size;
    padding: 15px;
    position: relative;

    &:hover,
    &:focus {
      background-color: $color-moinworld;
      border-color: $color-moinworld;
      color: #fff;
    }

    & .icon-bar {
      background-color: #888;
    }

    &__round {
      border-radius: 50%;
      border: 1px solid $color-moinworld;
      line-height: 0;
    }
  }
}

.navbar-announcement-bar {
  padding: 8px 16px;
  text-align: center;
  background-image: $gradient-moinworld;
  color: $color-white;
  letter-spacing: 1px;
}

.navbar-announcement-bar--emphasis {
  font-weight: 600;
  text-transform: uppercase;

}

a:hover.navbar-announcement-bar-link {
  text-decoration: underline;
  color: $color-white;
}
