/* Formatierung Codeschool Kurse Preis */
.border-top {
  border-top: 2px solid $color-mona-lisa;
  padding: 16px;

  @include bp-medium {
    padding: 0;
  }
}

.course-table {
  text-align: center;
  padding-top: 64px;

  &__row {
    @include bp-medium {
      height: auto;
    }
  }

  &__col {
    height: inherit;

    @include bp-medium {
      padding-bottom: 16px;
    }

    p {
      position: relative;
      top: 17%;


      @include bp-medium {
        text-align: left;
      }
    }
  }

  h4 {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 700;
    margin-top: 0;

    @include bp-medium {
      text-align: left;
    }
  }
}
