.logo-grid {
  .mb-section__header ~ .mb-section__header {
     margin-top: 64px;
  }
}

.logo-grid--home {
  margin-top: 64px;
}

.logo-grid__item {
  object-fit: contain;
  width: 100%;
  max-width: 250px;
  place-self: center;
  filter: saturate(0);
}

.logo-grid__link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-grid--fiveLogos {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

  .logo-grid__item {
    max-height: 110px;
  }
}

.logo-grid--sixLogos {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}
