h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 1.2;
  color: $color-emperor;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  margin-top: 20px;
  margin-bottom: 20px;
}

h1,
.h1 {
  font-size: 48px;
  letter-spacing: -0.5px;
  line-height: 1.2;

  @media (min-width: 768px) {
    font-size: 60px;
  }
}


h2,
.h2 {
  font-size: 28px;
  text-transform: none;
  letter-spacing: 0;
  text-align: center;
  font-weight: 300;

  @media (min-width: 768px) {
    font-size: 48px;
  }
}

h3,
.h3 {
  font-size: 21px;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 34px;
  }
}

h4,
.h4 {
  font-size: 24px;
  color: $color-emperor;
  text-align: center;
  line-height: 1.5;
}

p {
  margin: 0 0 10px;
  font-size: 16px;
  color: $color-emperor;
  letter-spacing: 0.5px;
  line-height: 1.5;
}

.lead {
  // margin-bottom: 20px;
  font-size: 18px;
  color: $color-emperor;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 21px;
  }
}


.text-small {
  font-size: 12px;
}

.text-large {
  font-size: 18px;
}

.text-italic {
  font-style: italic;
}

.text-primary {
  color: $color-moinworld;
}

a.text-primary:hover {
  color: #2c8494;
}

.bg-primary {
  color: #fff;
  background-color: $color-moinworld;
}

a.bg-primary:hover {
  background-color: #2c8494;
}

abbr[title],
abbr[data-original-title] {
  border-bottom: 1px dotted #999999;
}

blockquote {
  padding: 10px 20px;
  margin: 0 0 20px;
  font-size: 14px;
  border-left: 5px solid $color-moinworld;
}

blockquote footer,
blockquote small,
blockquote .small {
  display: block;
  font-size: 80%;
  line-height: 1.42857143;
  color: #999999;
}

blockquote footer:before,
blockquote small:before,
blockquote .small:before {
  content: '\2014 \00A0';
}

.blockquote-reverse,
blockquote.pull-right {
  border-right: 5px solid $color-moinworld;
}

address {
  margin-bottom: 20px;
  line-height: 1.42857143;
}

.overline {
  font-size: 20px;
  color: $color-mulberry;
  letter-spacing: 1.5px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
}

.heading {
  margin-bottom: 40px;
}

.heading h1 i.fa,
.heading h2 i.fa,
.heading h3 i.fa,
.heading h4 i.fa,
.heading h5 i.fa {
  display: inline-block;
  background: $color-moinworld;
  width: 30px;
  height: 30px;
  vertical-align: middle;
  text-align: center;
  color: #fff;
  font-size: 12px;
  line-height: 30px;
  border-radius: 15px;
}
