.newsletterSignup {
  @include bp-small {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  &__image {
    background-position: center;
    background-size: cover;
  }

  &__form {
    padding: 3em;
    background-color: $color-gallery;

    @include bp-small {
      padding: 7em 3em;
    }
  }
}

.newsletterSignup__input {
  width: 100%;
  height: 46px;
  max-width: 450px;
  padding: 6px 22px;
  margin-top: 1em;
  font-size: 14px;
  line-height: 1.428571429;
  color: #555555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 25px;
  border: none;
  background: #FFFFFF;
  box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.06);
}

.newsletterSignup__submit {
  width: 100%;
  max-width: 450px;
  margin-left: 0.25em;
  margin-top: 1em;
}

.newsletterSignup--footer {
  display: block;

  h4 {
    text-align: left;
    margin-top: -16px;
  }

  p {
    color: $color-white;
    opacity: 0.7;
    text-align: left;
  }

  .newsletterSignup__form {
    padding: 0;
    background-color: transparent;
  }
}

.newsletterSignup--default {
  .newsletterSignup__image {
    background-image: url(/img/code-farm2.jpg);
  }
}

.newsletterSignup--codeschool {
  margin-top: 2em;
  margin-bottom: 5em;

  .newsletterSignup__image {
    background-image: url(/img/code-farm2.jpg);
  }
}

.newsletterSignup--blockchain_page {
  .newsletterSignup__image {
    background-image: url(/img/moinblockchain/mb_speaker.jpg);
    background-position: right top;
  }
}
