.quoteStrip {
  margin-bottom: 32px !important;

  .grid {
    display: block;

    @include bp-medium {
      display: grid;
    }
  }
}

.quoteStrip__image {}

.quoteStrip__imageCaption {
  margin-top: 16px;
  font-size: 14px;
}

.quoteStrip__quote {
  position: relative;

  @include bp-medium {
    margin-left: -6em;
    padding-top: 32px;
  }

  &:before {
    content: url('/img/quote-open.svg');
  }

  &:after {
    content: url('/img/quote-close.svg');
    position: absolute;
    right: 0;
  }

  &:before, &:after {
    display: inline-block;
    transform: scale(0.5);

    @include bp-medium {
      transform: scale(0.7);
    }
  }

  h3 {
    margin: 0;
  }
}
