.hero-teaser {
  $gap: 15px;
  background: url(/img/heading/heading-fallback.jpg) center 27% no-repeat; //fallback image
  background-size: cover;
  text-align: center;
  box-shadow:inset 0 0 0 2000px rgba(43,43,71,0.3);
  height: 55vh;

  &--video {
    overflow: hidden;
    height: 90vh;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: $gradient-moinworld;
    opacity: .6;
    z-index: 1;
  }

  &__content {
    bottom: $gap;
    position: absolute;
    width: 100%;
    z-index: 1;

    @media screen and (min-width: 992px) {
      padding-right: $gap;
      text-align: right;
    }
  }

  &__title {
    color: $color-white;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: .08em;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
  }
}

.hero-video {
  position: relative;
  max-width: 100vw;
  height: 100%;
  top: 0;
  left: -100%;

  @media screen and (min-width: 500px) {
    left: 0;
  }

  video {
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: 90vh;
    z-index: -100;

    @media screen and (min-width: 1150px) {
      width: 100vw;
      height: auto;
    }
  }
}

.hero-video__overlay {
  position: absolute;
  top: 50px;
  z-index: 2;
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  h2, h3, h4, p {
    max-width: 1000px;
  }
}

.hero-video__mbLogo {
  margin-top: -5em;
}

.hero-video__dateLocation {
  margin-top: 32px;
  font-size: 20px;
  color: $color-white;
  letter-spacing: 1.5px;
  text-align: center;
  text-transform: uppercase;
}

.hero-video__cta {
  margin-top: 32px;
}
