.person {
  $image-size: 150px;

  &-wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  flex: 0 0 100%;
  margin-bottom: $gutter / 2;
  text-align: center;

  @include breakpoint($screen-sm) {
    flex-basis: 50%;
  }

  @include breakpoint($screen-md) {
    flex-basis: 33%;
  }

  @include breakpoint($screen-lg) {
    flex-basis: 25%;
  }

  &__image {
    background-color: $color-grey-light;
    border-radius: 50%;
    display: block;
    height: $image-size;
    margin: 0 auto;
    overflow: hidden;
    width: $image-size;
  }
}
