.box-group {

  @include breakpoint($screen-sm) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: stretch;
  }

  margin-bottom: $gutter / 2;

  &__item {
    @include breakpoint($screen-md) {
      flex-grow: 0;
      flex-basis: auto;
      width: calc(33% - #{$gutter / 2});
    }

    flex: 1 0 50%;

    &--dark {
      background: 0 $color-fiord;
    }
  }
}
