a {
  color: $color-moinworld;
  text-decoration: none;
}

a:hover,
a:focus {
  color: $color-moinworld;
  text-decoration: underline;
}

a:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

.btn {
  font-weight: 700;
  padding: 14px 22px;
  font-size: 14px;
  line-height: 1.42857143;
  border-radius: 25px;
  box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.06);

  &:hover {
    box-shadow: 2px 2px 10px 0 rgba(0,0,0,0.12);
  }
}

.input-group .btn {
  font-size: 14px;
}

.btn-lg {
  padding: 18px 28px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 45px;
}

.btn-sm {
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
}

.btn-xs {
  padding: 1px 5px;
  font-size: 12px;
  line-height: 1.5;
}

.btn-primary {
  background-color: $color-fiord;
  color: $color-white;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  border: 2px solid transparent;
  transition: all 0.3s;
}

.btn-primary:hover {
  background-color: $color-white;
  color: $color-fiord;
  text-decoration: none;
  border: 2px solid $color-fiord;
}

.btn-primary:active {
  background-color: $color-white !important;
  color: $color-fiord !important;
  text-decoration: none !important;
}

.btn-primary:focus {
  background-color: $color-white;
  color: $color-fiord;
  text-decoration: none;
}

.btn-primary__white {
  background-color: $color-white;
  border-color: $color-moinworld;
}

.btn-secondary {
  background-color: $color-mona-lisa;
  color: $color-emperor;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  border: none;
  transition: all 0.3s;
}

.btn-secondary:hover {
  background-color: lighten($color-mona-lisa, 3%);
  text-decoration: none;
}

.btn-secondary:active {
  background-color: lighten($color-mona-lisa, 3%);
  text-decoration: none;
}

.btn-secondary:focus {
  background-color: lighten($color-mona-lisa, 3%);
  text-decoration: none;
}

.inline-link {
  font-size: 14px;
  color: $color-fiord;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 700;
}

.inline-link:hover {
  color: $color-fiord;
  text-decoration: underline;
}

.inline-link:active {
  color: $color-fiord;
  text-decoration: underline;
}

.inline-link:focus {
  color: $color-fiord;
  text-decoration: underline;
}

.inline-link-lg {
  font-size: 24px;
}

