    padding-left: 7vh;
    padding-right: 7vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
    margin: 2vh;
}

.small {
  width: 60%;
}

/* WTM-Logo Style home page */

.img-wtm-size {
    max-width: 65%;
    height: auto;
}

.img-wtm-size-blog {
    max-width: 100%;
    height: 50%;
}

/* Grid */

.grid {
  display: grid;
  margin: 0 auto;
  grid-gap: 16px;
  width: 90%;
}

.grid-auto {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.font-grid {
    text-transform: uppercase;
    line-height: 14px;
    letter-spacing: 0.3em;
    color: $color-mulberry;
}

.flex {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
  align-items: center;
}

.flexchild {
  width:50%;

}

.gridcol {
  font-size: 15px;
  padding: 10px;
}




.row-pics {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}


/* Create four equal columns that sits next to each other */
.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
}

/* Schlerinnen button */

.pink-brd {
    border: 2px solid #dc5ef2;
    background-color: white;
    color: black;
}


/* Booking Styles */

.intrinsic-container {
  position: relative;
  overflow: hidden;
}

.meetupmodal {
  height: 750px;
}

.codeschoolupmodal {
  height: 1000px;
}

/* 16x9 Aspect Ratio */
.intrinsic-container-16x9 {
  padding-bottom: 56.25%;
}

/* 4x3 Aspect Ratio */
.intrinsic-container-4x3 {
  padding-bottom: 75%;
}

.intrinsic-container iframe {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal-backdrop {
    z-index: 0;
}

.box-image-text .image {
  height: 250px;

  @include bp-small {
    height: 200px;
  }

  @include bp-medium {
    height: 150px;
  }
}

/* Booking Styles Ende */



