.cta-banner {
  margin:32px;
  padding: 32px;
  position: relative;
  text-align: center;
  background-color: $color-white;

  &:before {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    background: url(/img/banners/html2.jpg) center 20% no-repeat; //fallback image
    background-size: cover;
    margin: -32px; /* !important */
  }

  &:after {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    background-image: $gradient-moinworld;
    opacity: .6;
    margin: -32px; /* !important */
  }

  @media only screen and (min-width: 768px) {
    margin: 64px;

    &:before {
      margin: -64px;
    }

    &:after {
      margin: -64px;
    }
  }

  h2 {
    margin-bottom: 64px;
  }
}

// === CTA banner moinblockchain ===
.mb-cta-banner {
  &:before {
    background: url(/img/moinblockchain/mb_2018_audience.jpg) center 20% no-repeat;
    background-size: cover;
  }
}

.mb-cta-banner__logo {
  margin-bottom: 32px;
}

.mb-cta-banner__cta {
  margin-top: 32px;
}
