.mb-section {
  padding-top: 32px;
  padding-bottom: 32px;
  margin: 0 !important;

  @include bp-medium {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  h2 {
    margin-top: 0;
    margin-bottom: 16px;
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
  }

  .lead {
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
  }
}

.mb-section__header {
  margin-bottom: 32px;
}


// === Logo ===
.mb-logo {
  width: 50%;
  max-width: 500px;
}

@media only screen and (max-width: 768px) {
  .mb-logo {
    width: 100%;
    padding-left: 1em;
    padding-right: 1em;
  }
}

// === Speaker grid ===
.speaker-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  & ~ .mb-section__header {
    padding-top: 64px;
  }
}

.speaker-grid {
  .speaker-grid__person {
    flex: 0 0 calc(25% - 16px);
    padding: 16px;
    margin: 10px 0 32px;
  }

  &--5 {
    .speaker-grid__person {
      flex: 0 0 calc(20% - 16px);
    }
  }
}



.speaker-grid__img {
  margin-bottom: 32px;
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
}

h4.speaker-grid__name {
  margin-bottom: 4px;
}

a > .speaker-grid__name {
  color: #5927e5;
}


p.speaker-grid__title {
  margin-bottom: 0;
  opacity: 0.8;
}

.speaker-grid__company {
  margin-bottom: 16px;
  font-style: italic;
  opacity: 0.8;
}

.speaker-grid__description {
}

.speaker-grid__social {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 16px;
}

.speaker-grid__socialLink {
  font-size: 0;
  padding-left: 8px;
  padding-right: 8px;
}

.speaker-grid__socialLink:hover {
  opacity: 0.8;
}

// === Videos ===
.mb-videos {
  .inline-link {
    display: inline-block;
    margin-top: 16px;
  }
}

// === Progress stepper ===
.progress-stepper {
  grid-row-gap: 64px;
}

.progress-stepper__step {}

.progress-stepper__number {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
  width: 122px;
  height: 122px;
  background-color: $color-white;
  font-size: 48px;
  color: $color-mulberry;
  letter-spacing: 0;
  text-align: center;
  line-height: 57.6px;

  &:before {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    margin: -3px; /* !important */
    border-radius: inherit; /* !important */
    background: $gradient-moinworld;
  }
}

.progress-stepper__description {
  margin-top: 16px;
}

.progress-stepper__cta {
  margin-top: 16px;

  @media only screen and (min-width: 768px) {
    margin-top: 32px;
  }
}

.mb-grid-image {
  width: 100%;
}

// === Speaker detail page ===
.mb-speaker-detail {
  padding-bottom: 64px;

  h1, h2, h3, h4 {
    text-align: left;
  }
}

.mb-speaker-detail--img {
  width: 100%;
  max-width: 250px;
}

.mb-speaker-detail--header {
  padding-bottom: 16px;
  max-width: 750px;

  h1, h2, h3, h4 {
    margin: 0;
  }
}

.mb-speaker-detail--description {
  margin-top: 16px;
}

.mb-speaker-detail--content {
  p {
    margin-bottom: 2em;
  }
}

.mb-speaker-detail--sidebar {
  padding-top: 32px;
}

.mb-stage {
  padding: 16px;

  h4 {
    text-align: left;
  }
}
